.how-works{background:#faf7f3;padding:70px 0}.how-works .heading{margin-bottom:48px}.how-works__wrapper{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center}@media (max-width:640px){.how-works__wrapper{-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-align:center;-ms-flex-align:center;align-items:center}}.how-works__item{max-width:260px}.how-works__icon{position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;margin-bottom:20px;min-height:95px}.how-works__icon img{max-width:100%}.how-works__text{font-size:16px;line-height:24px;text-align:center;color:#50494b}