.cakes{padding-top:80px;padding-bottom:40px}@media (max-width:854px){.cakes{padding-top:40px}}.cakes .cakes__layout{max-width:1126px;margin-left:auto;margin-right:auto;padding-left:15px;padding-right:15px}.cakes .cakes-grid{display:grid;grid-gap:24px;grid-template-columns:repeat(4,minmax(190px,1fr))}@media (max-width:854px){.cakes .cakes-grid{grid-template-columns:repeat(auto-fit,minmax(190px,1fr))}}.cakes .heading{margin-bottom:38px}.cakes .cakes-image{position:relative;border-radius:12px;overflow:hidden;padding-top:100%;margin-bottom:20px}.cakes .cakes-image img{position:absolute;top:0;left:0;width:100%;height:100%;-o-object-fit:cover;object-fit:cover}.cakes .button{width:100%}