.our-story{padding:80px 0}@media (max-width:854px){.our-story{padding:70px 0}}.our-story__wrapper{display:grid;grid-gap:70px;grid-template-columns:repeat(2,minmax(250px,1fr))}@media (max-width:854px){.our-story__wrapper{grid-gap:24px}}@media (max-width:640px){.our-story__wrapper{grid-template-columns:repeat(1,minmax(250px,1fr))}}.our-story__image{border-radius:12px;overflow:hidden;display:inline-block}.our-story__image img{max-width:100%}.our-story__content{color:#353132}.our-story__content h3{font-family:Leckerli One,cursive;font-size:48px;line-height:60px;margin-bottom:14px}.our-story__content p{font-size:16px;line-height:24px;color:#50494b;margin-bottom:12px}.our-story__content .our-story__list li{position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.our-story__content .our-story__list li:before{width:8px;height:8px;background:#d57792;content:"";display:block;border-radius:50%;margin-right:8px}.our-story__content .our-story__list li:hover:before{background:#b53057}.our-story__content .our-story__list li:hover a{color:#b53057}.our-story__content .our-story__list a{font-size:16px;line-height:24px;color:#d57792}.our-story__list li .icon-arrow{opacity:0;visibility:hidden;-webkit-transform:translate(-10px);-ms-transform:translate(-10px);transform:translate(-10px)}.our-story__list li:hover .icon-arrow{opacity:1;visibility:visible;-webkit-transform:translate(4px);-ms-transform:translate(4px);transform:translate(4px)}