.instagram{padding:80px 0 115px}.instagram .heading{margin-bottom:48px}.instagram__grid{display:grid;grid-template-columns:repeat(3,minmax(200px,1fr));grid-gap:6px}@media (max-width:767px){.instagram__grid{grid-template-columns:repeat(1,minmax(200px,1fr));max-width:540px;margin:0 auto}}.instagram__grid-inside{display:grid;grid-template-columns:repeat(2,minmax(120px,1fr));grid-gap:6px}@media (max-width:767px){.instagram__grid-inside{grid-template-columns:repeat(2,minmax(120px,1fr));margin:0 auto;padding:0 30px}}.instagram__item--el{position:relative;padding-top:100%;border-radius:12px;overflow:hidden}.instagram__item--el img{position:absolute;top:0;left:0;width:100%;height:100%;-o-object-fit:cover;object-fit:cover}.instagram__item--el-1{grid-column:1/-1}.instagram__item .instagram__icon-item{position:absolute;top:10px;right:10px;z-index:20}.instagram__item .instagram__icon-item .icon-inst{width:20px}.instagram__col-4:nth-child(2n) .instagram__item--el-1{grid-column:1/1}.instagram__col-4:nth-child(2n) .instagram__item--el-3{grid-column:1/-1}