.experience{padding:70px 0;background:#f6f3f5}.experience .heading{margin-bottom:40px}.experience .experience__wrapper{display:grid;grid-gap:74px;grid-template-columns:repeat(3,minmax(250px,1fr))}@media (max-width:960px){.experience .experience__wrapper{grid-gap:20px;grid-template-columns:repeat(3,minmax(200px,1fr))}}@media (max-width:854px){.experience .experience__wrapper{display:block}}.experience__button{position:absolute;top:50%;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%)}.experience__button--prev{left:0}.experience__button--next{right:0}@media (max-width:854px){.experience__item{max-width:280px;margin:0 auto}}.experience .experience__image{min-height:100px;position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;margin-bottom:15px}.experience .experience__content{font-size:16px;line-height:24px;text-align:center;color:#353132}