.button{display:-webkit-inline-box;display:-ms-inline-flexbox;display:inline-flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;min-height:42px;padding:10px 20px;border-radius:12px;font-size:16px;text-align:center;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out;cursor:pointer;border:none}@media (max-width:854px){.button{min-height:40px;padding:8px 12px}}.button__big{min-height:48px}.button__pink{background:#d57792;color:#fff;border:1px solid #d57792}.button__pink svg{margin-right:8px;fill:#fff;-webkit-transition:none;-o-transition:none;transition:none}.button__pink:hover{background:rgba(0,0,0,0);color:#d57792}.button__pink:hover svg{fill:#d57792}.button__border-pink{border:1px solid #d57792;color:#d57792}.button__border-pink svg{stroke:#d57792}.button__border-pink:hover{background:#d57792;color:#fff}.button__border-pink:hover svg{stroke:#fff}.button.button__icon-right svg{margin-left:10px}.button__pink-light{background:#d57792;color:#fff;border:1px solid #d57792}.button__pink-light:hover{background:#b53057;border:1px solid #b53057}